
import '../src/styles/dashboard.css';
import React,{useState} from 'react';
// import Grid from '@material-ui/core/Grid';
// import CardContent from "@material-ui/core/CardContent";


function SearchBar(){
    
    const[zip1,setZip1] = useState('');
    const[zip2,setZip2] = useState('');
    const[weight,setweight] = useState('');
    const[submit, setSubmit] = useState(false);
    const[data, setData] = useState('');
        function send_server(e){
        
        let msg = ['Error! Following fields are empty: \n']
        console.log('Submitted form ');
        setSubmit(true);
        if (zip1.length >5 || zip1.length <3){
            msg +='Check sender zipcode'
        }
        else if (zip2.length >5 || zip2.length <3){
            msg +='Check reciever zipcode'
        }
        if (msg.length >1) {
           window.alert(msg);
           setSubmit(false);
           e.preventDefault();
           console.log(weight);
        }
        

        var myHeaders = new Headers();
        myHeaders.append(  'Accept', 'accept.jpeg');
    
        fetch("https://shippingapinodejs.herokuapp.com/details", {
             headers: myHeaders }) //1
        .then((response) => response.json()) //2
        .then((user) => {
          console.log(user);
          setData(user);
          });
         
    }

    function reset_search(e){
        setSubmit(false);
    }

    function displayCard(cost, days){
        return (
            <span id= "container">
                <span id = "card">
                    <h3>Estimated Analysis</h3> {"\n"}
                    Cost of shipping: {cost}  {"\n"}
                    Days to ship package: {days}  {"\n"}
                    </span>
            </span>
        )
    }
    let  all_cards = " ";

    if (data !== ''){
    
        { all_cards =data.map((sample)=>
            <span id= "container">
                <span id = "card">
                    <h3>Estimated Analysis</h3> {"\n"}
                    Cost of shipping: {sample.rate.MonetaryValue}  {"\n"}
                    Days to ship package: {sample.days.BusinessDaysInTransit}  {"\n"}
                    </span>
            </span>
        )}

    }



 if (submit === false){
    return(
     <div>
         <p id = "info"> Enter following fields to get an estimate of shipping cost and duration !  </p>

     <form id ="get-info" > 
       <input type ="text" placeholder ="Enter sender zipcode" onChange={e => setZip1(e.target.value)} />
            <br/>
            <input type ="text" placeholder ="Enter receiver zipcode" onChange={e => setZip2(e.target.value)}  />
            <br/>
            <input type ="text" placeholder ="Enter package(lb)" onChange={e => setweight(e.target.value)}/>
            <br/>
            <input type="number" placeholder ="h (in)"/>
            <input type="number" placeholder ="b (in)"/>
            <input type="number" placeholder ="w (in)"/>
            <br/>     
        <input type="submit" value="Submit" onClick= {send_server}/>
         </form> 
     </div>
    );
    }
    else{
        return (
            <div>
            {all_cards}
            <button id ="back-button" onClick ={reset_search}> Click here to make new search! </button>
            </div>
        );
    }

}

export default SearchBar;