import '../src/styles/Login.css';
import firebase from './firebase';
import React,{useState} from 'react';
import {Link} from 'react-router-dom';

function Login() {

  const[email,setEmail] = useState('');
  const[password,setPassword] = useState('');
  const[logout,setLogout] = useState(false)
  const[user_name,setName] = useState('');

  function logout_attempt(e){
    firebase.logout()
    setTimeout(()=>setLogout(false),500)  
  }
  async function login_attempt(e){
    e.preventDefault();
    
    try{
      await firebase.login(email,password)
      setLogout(true); 
      setName(firebase.getUsername());
      }catch(error){
     window.alert(error.message)
    }
  }

  if ( logout === false){
  
    return (
    
   <div id ="login-info">

    <div id ="header">
      Welcome back to shipping services
    </div>
    <form id = "form2" align="center" onSubmit = {login_attempt}>
      <input type ="text" id ="userid" placeholder ="Username" onChange={e => setEmail(e.target.value)}/>
      <br/>
      <input type ="text" id ="passid" placeholder ="Password"  onChange={e => setPassword(e.target.value)}/>
      <br/> 

      <button id ="sign-in"> Sign in </button>                     
      <br/> 
    </form>
    
    <div id ="new-user">New User ?         
      <br/>
      <Link to ="/signup">
      <button id ="sign-up">
       Click here to sign up ! 
      </button>
      </Link>
   </div>

   </div>
  );
  }

  else {
    return(
    <div>
        <div id = "user-info">
        User name : {user_name} <br/>
        < button id = "logout" onClick={logout_attempt}>
          Click here to logout 
        </button>
      </div>
    </div>
    );
  }

}

export default Login;
