import firebase from './firebase';
import '../src/styles/Signup.css';
import {Link} from 'react-router-dom';

function Signup(){
    
    async function createAccount(e){
        let password = document.getElementById("password").value
        let email = document.getElementById("username").value
        let name = document.getElementById("firstname").value
         let msg = ['Error! Following fields are empty: \n']
        if ( name === ""){
            msg+='First name not entered';
          }
        else if (document.getElementById("lastname").value === ""){
            msg+='Lastname name not entered';
          }
        else if (document.getElementById("username").value === ""){
            msg+='Username name not entered';
        }
        else if (password === ""){
            msg+='Password not entered';
        }
        else if (password.length < 7){
            msg+='Password is too short';
        }
        if (msg.length >1) {
        window.alert(msg);
        e.preventDefault();
        }

        try{
            await firebase.register(email, password,name)
        }
        catch(error){
            window.alert(error.message)
        }
       
    }
    return(
    <div>
        <div class ="header"> Create an account </div>

        <div id = "input-box">
            <form id ="form1">
              <input type ="text"  placeholder ="First name" id="firstname"/>
              <br/> 
              <input type ="text" placeholder ="Last name" id ="lastname"/>
              <br/> 
              <input type ="text" placeholder ="Username" id ="username"/>
              <br/> 
              <input type ="text" placeholder ="Country" id ="=country"/>
              <br/> 
              <input type ="text" placeholder ="Password" id ="password"/>
              <br/> 
              <input type ="text" placeholder ="Confirmed Password" id ="c-password"/>
             <br/> 
             <Link to ="/login">
             <button class ="newAcc" onClick ={createAccount}> Click here to sign up ! </button>
             </Link>
            </form>
        </div>
    </div>
    );
}

export default Signup;