import '../src/styles/Navbar.css';
import {Link} from 'react-router-dom';

function Navbar(){

    return(     
    <div id="navbar">
        <Link to ="/"><p id ="home">Home</p></Link>
        <Link to ="/dashboard"><p id ="dashboard" >Dashboard</p></Link>
        <Link to ="/login"><p id ="login">Sign in </p></Link>
    </div>
    );
}

export default Navbar;

